import fs from 'fs/promises';
import fetch from 'node-fetch';

export async function readProducts() {
  const data = await fs.readFile('./app/data/products.json', 'utf-8');
  return JSON.parse(data);
}

export async function createProducts(shop, accessToken) {
  const products = await readProducts();

  for (const product of products) {
    const res = await fetch(`https://${shop}/admin/api/2023-07/products.json`, {
      method: 'POST',
      headers: {
        'X-Shopify-Access-Token': 'a1d03f7890d80a36637785aa69a0c668-1752152787',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ product }),
    });

    if (!res.ok) {
      const errText = await res.text();
      console.error('Failed creating product:', errText);
      continue;
    }

    const json = await res.json();
    console.log('Created product:', json.product.title);
  }
}
